      // YAMLå½¢å¼ã®è¨­å®šãƒ•ã‚¡ã‚¤ãƒ«ã‚’JavaScriptã‚ªãƒ–ã‚¸ã‚§ã‚¯ãƒˆã«å¤‰æ›
      const config = parseYaml<BookConfig>(configContent);

      // è¡¨ç´™ç”»åƒã®URLã‚’æ§‹ç¯‰
      // GitHubã®Raw Content APIã‚’ä½¿ç”¨ã—ã¦ç”»åƒã«ã‚¢ã‚¯ã‚»ã‚¹ã§ãã‚‹URLã‚’ç”Ÿæˆ
      const coverImagePath = `${RAW_CONTENT_URL}/books/${dir.name}/cover.png`;

      // æœ¬ã®ãƒ‡ã‚£ãƒ¬ã‚¯ãƒˆãƒªå†…ã®ç« ä¸€è¦§ã‚’å–å¾—
      const chapterContents = await fetchDirectoryContents(`books/${dir.name}`);
      
      // ç« ã¨ã—ã¦æ‰±ã†ãƒ•ã‚¡ã‚¤ãƒ«ã®ã¿ã‚’ãƒ•ã‚£ãƒ«ã‚¿ãƒªãƒ³ã‚°ãƒ»ã‚½ãƒ¼ãƒˆ
      const chapters = chapterContents
