  // è¨­å®šãƒ•ã‚¡ã‚¤ãƒ«ãŒå­˜åœ¨ã—ãªã„æœ¬ãªã©ãŒé™¤å¤–ã•ã‚Œã‚‹
  return books.filter((book) => book !== null);
