/**
 * ç‰¹å®šã®æœ¬ã®è©³ç´°æƒ…å ±ã¨ç« ã®å†…å®¹ã‚’å–å¾—ã™ã‚‹
 * 
 * @param slug - æœ¬ã®ã‚¹ãƒ©ãƒƒã‚°ï¼ˆãƒ‡ã‚£ãƒ¬ã‚¯ãƒˆãƒªåï¼‰
 * @returns Promise<Book | null> - æœ¬ã®è©³ç´°æƒ…å ±ã€å­˜åœ¨ã—ãªã„å ´åˆã¯null
 * 
 * ã“ã®é–¢æ•°ã¯ã€æœ¬ã®è©³ç´°ãƒšãƒ¼ã‚¸ã§ä½¿ç”¨ã•ã‚Œã‚‹é–¢æ•°ã§ã™ã€‚
 * fetchBooks()ã¨ã¯ç•°ãªã‚Šã€ç« ã®å®Ÿéš›ã®å†…å®¹ã‚‚å«ã‚ã¦å–å¾—ã—ã¾ã™ã€‚
 * 
 * å‡¦ç†ã®æµã‚Œï¼š
 * 1. æœ¬ã®è¨­å®šãƒ•ã‚¡ã‚¤ãƒ«ï¼ˆconfig.yamlï¼‰ã‚’å–å¾—ãƒ»è§£æ
 * 2. æœ¬ã®ãƒ‡ã‚£ãƒ¬ã‚¯ãƒˆãƒªå†…ã®ç« ä¸€è¦§ã‚’å–å¾—
 * 3. å„ç« ã®å†…å®¹ã‚’å–å¾—ãƒ»è§£æ
 * 4. æœ¬ã®è©³ç´°æƒ…å ±ã¨ç« ã®å†…å®¹ã‚’çµ±åˆã—ã¦è¿”ã™
 * 
 * æ³¨æ„ï¼šã“ã®é–¢æ•°ã¯ç« ã®å†…å®¹ã‚‚å«ã‚€ãŸã‚ã€fetchBooks()ã‚ˆã‚Šã‚‚é‡ã„å‡¦ç†ã«ãªã‚Šã¾ã™ã€‚
 * æœ¬ä¸€è¦§è¡¨ç¤ºã§ã¯fetchBooks()ã‚’ä½¿ç”¨ã—ã€è©³ç´°è¡¨ç¤ºã§ã®ã¿ã“ã®é–¢æ•°ã‚’ä½¿ç”¨ã™ã‚‹ã“ã¨ã‚’æ¨å¥¨ã—ã¾ã™ã€‚
 */
export async function fetchBook(slug: string): Promise<Book | null> {
  // æœ¬ã®è¨­å®šãƒ•ã‚¡ã‚¤ãƒ«ï¼ˆconfig.yamlï¼‰ã‚’å–å¾—
  // ã“ã®ãƒ•ã‚¡ã‚¤ãƒ«ã«ã¯æœ¬ã®ãƒ¡ã‚¿ãƒ‡ãƒ¼ã‚¿ãŒå«ã¾ã‚Œã‚‹
  const configContent = await fetchFileContent(`books/${slug}/config.yaml`);

  // è¨­å®šãƒ•ã‚¡ã‚¤ãƒ«ãŒå­˜åœ¨ã—ãªã„å ´åˆã¯nullã‚’è¿”ã™
  // ã“ã‚Œã«ã‚ˆã‚Šã€å­˜åœ¨ã—ãªã„æœ¬ã¸ã®ã‚¢ã‚¯ã‚»ã‚¹ã‚’é©åˆ‡ã«å‡¦ç†
  if (!configContent) {
    return null;
  }

  // YAMLå½¢å¼ã®è¨­å®šãƒ•ã‚¡ã‚¤ãƒ«ã‚’JavaScriptã‚ªãƒ–ã‚¸ã‚§ã‚¯ãƒˆã«å¤‰æ›
  const config = parseYaml<BookConfig>(configContent);

  // æœ¬ã®ãƒ‡ã‚£ãƒ¬ã‚¯ãƒˆãƒªå†…ã®ç« ä¸€è¦§ã‚’å–å¾—
  const contents = await fetchDirectoryContents(`books/${slug}`);

  // ç« ã¨ã—ã¦æ‰±ã†ãƒ•ã‚¡ã‚¤ãƒ«ã®ã¿ã‚’ãƒ•ã‚£ãƒ«ã‚¿ãƒªãƒ³ã‚°ãƒ»ã‚½ãƒ¼ãƒˆ
  const chapterFiles = contents
    .filter(
      (item) =>
        item.type === "file" &&
        item.name.endsWith(".md") &&
        !item.name.startsWith(".")
    )
    .sort((a, b) => {
      // ãƒ•ã‚¡ã‚¤ãƒ«åã®æ•°å­—éƒ¨åˆ†ã§ã‚½ãƒ¼ãƒˆï¼ˆ1.intro.md, 2.setup.md ãªã©ï¼‰
      // ã“ã‚Œã«ã‚ˆã‚Šã€ç« ãŒæ­£ã—ã„é †åºã§è¡¨ç¤ºã•ã‚Œã‚‹
      const aNum = parseInt(a.name.split(".")[0]) || 0;
      const bNum = parseInt(b.name.split(".")[0]) || 0;
      return aNum - bNum;
    });

  // ç« ãŒå­˜åœ¨ã—ãªã„å ´åˆã¯nullã‚’è¿”ã™
  // ã“ã‚Œã«ã‚ˆã‚Šã€å†…å®¹ã®ãªã„æœ¬ã‚’é©åˆ‡ã«å‡¦ç†
  if (chapterFiles.length === 0) {
    return null;
  }

  // å„ç« ã®å†…å®¹ã‚’ä¸¦åˆ—ã§å–å¾—ãƒ»å‡¦ç†
  // Promise.allã‚’ä½¿ç”¨ã™ã‚‹ã“ã¨ã§ã€è¤‡æ•°ã®ç« ã‚’åŒæ™‚ã«å‡¦ç†ã—ã€ãƒ‘ãƒ•ã‚©ãƒ¼ãƒãƒ³ã‚¹ã‚’å‘ä¸Š
  const chapters = await Promise.all(
    chapterFiles.map(async (file, index: number) => {
      // ç« ãƒ•ã‚¡ã‚¤ãƒ«ã®å†…å®¹ã‚’å–å¾—
      const markdown = await fetchFileContent(`books/${slug}/${file.name}`);

      // ãƒ•ã‚¡ã‚¤ãƒ«ãŒå­˜åœ¨ã—ãªã„å ´åˆã¯nullã‚’è¿”ã™
      if (!markdown) {
        return null;
      }

      // Markdownã‚’è§£æã—ã¦ãƒ•ãƒ­ãƒ³ãƒˆãƒã‚¿ãƒ¼ã¨HTMLåŒ–ã•ã‚ŒãŸå†…å®¹ã‚’å–å¾—
      const { frontMatter, content } = await parseMarkdown(markdown);

      // ãƒãƒ£ãƒ—ã‚¿ãƒ¼ã®ã‚¹ãƒ©ãƒƒã‚°ã¯ã€ãƒ•ã‚¡ã‚¤ãƒ«åã‹ã‚‰æ‹¡å¼µå­ã‚’é™¤ã„ãŸã‚‚ã®
      // ã“ã‚Œã«ã‚ˆã‚Šã€URLã§ç« ã«ã‚¢ã‚¯ã‚»ã‚¹ã§ãã‚‹ã‚ˆã†ã«ãªã‚‹
      const chapterSlug = file.name.replace(".md", "");

      // ç« ãƒ‡ãƒ¼ã‚¿ã‚’æ§‹ç¯‰
      return {
        // ç« ã®ã‚¹ãƒ©ãƒƒã‚°ï¼ˆURLã§ä½¿ç”¨ï¼‰
        slug: chapterSlug,
        // ãƒ•ãƒ­ãƒ³ãƒˆãƒã‚¿ãƒ¼ã‹ã‚‰ã‚¿ã‚¤ãƒˆãƒ«ã‚’å–å¾—ã€ãªã‘ã‚Œã°ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆã®ç« ã‚¿ã‚¤ãƒˆãƒ«
        title: frontMatter.title || `Chapter ${index + 1}`,
        // HTMLåŒ–ã•ã‚ŒãŸç« ã®å†…å®¹
        content: content,
        // ç« ã®é †åºï¼ˆç›®æ¬¡è¡¨ç¤ºãªã©ã§ä½¿ç”¨ï¼‰
        order: index,
      };
    })
  );

  // æœ¬ã®è©³ç´°æƒ…å ±ã‚’æ§‹ç¯‰
  return {
    // æœ¬ã®ã‚¹ãƒ©ãƒƒã‚°ï¼ˆIDï¼‰
    id: slug,
    // è¨­å®šãƒ•ã‚¡ã‚¤ãƒ«ã‹ã‚‰ã‚¿ã‚¤ãƒˆãƒ«ã‚’å–å¾—ã€ãªã‘ã‚Œã°ã‚¹ãƒ©ãƒƒã‚°ã‚’ä½¿ç”¨
    title: config.title || slug,
    // è¨­å®šãƒ•ã‚¡ã‚¤ãƒ«ã‹ã‚‰èª¬æ˜ã‚’å–å¾—ã€ãªã‘ã‚Œã°ç©ºæ–‡å­—
    description: config.summary || "",
    // è¨­å®šãƒ•ã‚¡ã‚¤ãƒ«ã‹ã‚‰ã‚¿ã‚°ï¼ˆtopicsï¼‰ã‚’å–å¾—ã€ãªã‘ã‚Œã°ç©ºé…åˆ—
    tags: config.topics || [],
    // è¨­å®šãƒ•ã‚¡ã‚¤ãƒ«ã‹ã‚‰ä¾¡æ ¼ã‚’å–å¾—ã€ãªã‘ã‚Œã°0ï¼ˆç„¡æ–™ï¼‰
    price: config.price || 0,
    // ä¾¡æ ¼ãŒ0ã‚ˆã‚Šå¤§ãã„å ´åˆã«æœ‰æ–™ã‚³ãƒ³ãƒ†ãƒ³ãƒ„ã¨ã—ã¦åˆ¤å®š
    isPaid: !!config.price && config.price > 0,
    // è¡¨ç´™ç”»åƒã®URL
    coverImage: `${RAW_CONTENT_URL}/books/${slug}/cover.png`,
    // ç« ã®ä¸€è¦§ï¼ˆå‡¦ç†ã«å¤±æ•—ã—ãŸç« ã¯é™¤å¤–ï¼‰
    chapters: chapters.filter((chapter) => chapter !== null),
    // ç« ã®æ•°
    chapterCount: chapters.length,
    // å…¬é–‹æ—¥ã‚’å–å¾—ã€ãªã‘ã‚Œã°ç¾åœ¨ã®æ—¥æ™‚
    createdAt: config.published_at || new Date().toISOString(),
    // æ›´æ–°æ—¥ã‚’å–å¾—ã€ãªã‘ã‚Œã°å…¬é–‹æ—¥ã€ãã‚Œã‚‚ãªã‘ã‚Œã°ç¾åœ¨ã®æ—¥æ™‚
    updatedAt:
      config.updated_at || config.published_at || new Date().toISOString(),
  };
}